<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>数据导出</span>

            </div>
            <div>
                <el-select v-model="type" class='small' style="width:180px" size='small' placeholder="请选择">
                    <el-option v-for="(item,index) in typeList" :key="index" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
                <el-date-picker v-model="time" size='small' value-format='timestamp' type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
                <el-button size='small' @click="data_export" type="primary">导出</el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Home",
    data() {
        return {
            type: "",
            time: "",
            typeList: [
                {
                    id: 1,
                    name: "导出注册用户数"
                }
            ]
        };
    },
    computed: {},
    methods: {
        data_export() {
            if (!this.type) {
                this.$message({
                    type: "error",
                    message: "请选择类型!"
                });
                return;
            }
            if (!this.time) {
                this.$message({
                    type: "error",
                    message: "请选择时间!"
                });
                return;
            }

            let url;
            switch (this.type) {
                case 1:
                    url = "/admin/stat/export_register_order";
                    break;
            }
            let params = {
                notice: "导出成功！",
                startTime: this.time[0] / 1000,
                endTime: this.time[1] / 1000
            };
            const loading = this.$loading({
                lock: true,
                text: "Loading",
                background: "rgba(0, 0, 0, 0.7)"
            });
            this.$api(url, params).then(res => {
                loading.close();
                location.href = res.data.csvFilePath;
            });
        }
    },
    created() {
        // this.getFacilityDetail(16)
    }
};
</script>

<style lang='scss' scoped>
</style>



